import React, { Component } from 'react'
import { Link, Redirect } from "react-router-dom";
import logo from "./kbc.png"

export default class Header extends Component {

    constructor(props) {
        super(props);

        this.State = {
            enableUpdate: ""    // temporery state
        }

        this.prtformLogout = React.createRef();
        this.prtformLogin = React.createRef();
    }

    componentDidMount = () => {
        localStorage.setItem("enableUpdate", "");
        this.setState({enableUpdate: false});
    }

    componentDidUpdate = () => {

        if(this.prtformLogin.current) {

            console.log(this.prtformLogin.current);
            this.prtformLogin.current.onclick = () => {

                localStorage.setItem("enableUpdate", true);
                this.setState({enableUpdate: true});
                
            }
        }

        if(this.prtformLogout.current) {
            console.log(this.prtformLogout.current);
            this.prtformLogout.current.onclick = () => {
                localStorage.setItem("enableUpdate", false);
                localStorage.setItem("userLoginStatus", false)
                this.setState({enableUpdate: true});
            }
        }
    }

    render() {

        console.log("enable Update: ", localStorage.getItem("enableUpdate"));
        console.log("State", this.State);

        let participateButton, loginButton, logoutButton;

        if(this.props.participateBtn) {
            participateButton = <div className="col-auto">
                                    <Link className="btn btn-success" to="/survey" >Participate</Link>
                                </div>
        } else {
            participateButton = null;
        }

        if(this.props.loginBtn) {
            loginButton = <div className="col-auto">
                            <div className="btn btn-success" ref={this.prtformLogin} >Login</div>
                        </div>
        } else {
            loginButton = null;
        }

        if(this.props.logoutBtn) {
            logoutButton = <div className="col-auto">
                                <div className="btn btn-danger" to="/login" ref={this.prtformLogout} >Logout</div>
                            </div>
        } else {
            logoutButton = null;
        }

        if(localStorage.getItem("enableUpdate") === "true") { 
            return <Redirect to="/login" />
        }

        if(localStorage.getItem("enableUpdate") === "false") {
            return <Redirect to="/" />
        }
        
        return (
            
            <div className="row justify-content-center bg-primary">
                <div className="col-auto py-2">
                    <img src={logo} alt="logo" width="100px" />
                </div>
                <div className="col-auto py-3">
                    <h1 className="font-weight-bold title">Kon Banega karodpati</h1>
                </div>
                <div className="col-auto py-3 justify-content-end">
                    <div className="row mt-2">
                        {participateButton}
                        {loginButton}
                        {logoutButton}
                    </div>
                </div>
            </div>
        )
    }
}
