import React, { Component } from 'react'
import Home from "./Home"
import Login from "./Login"
import "./bootstrap.min.css"
import "./kbc.css"
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Participate from './participate'
import withLoginCheck from "./withLoginCheck"
import Survey from "./Survey"

class KBC extends Component {
    render() {
        return (
            <div>
                <Router>
                    <Switch>
                        <Route exact={true} path="KBC/" component={Home} />
                        <Route exact={true} path="KBC/login" component={Login} />
                        <Route exact={true} path="KBC/participate" component={Participate} />
                        <Route exact={true} path="KBC/survey" component={Survey} />
                    </Switch>
                </Router>
            </div>
        )
    }
}

// export default KBC;
export default withLoginCheck(KBC);