import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';

export default class LoginForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            enterEmail: "",
            enterPass:  "",
            navPath: "/login"
        }
        this.validateLogin = React.createRef();
    }

    componentDidUpdate() {
        console.log("Hello World");
    }

    userData = {
        email: "soni.jay008@gmail.com",
        pass:   "abcd"
    }

    render() {

        const handleLoginClick = () => {
                console.log("handle click invoked");
                // alert("Hello World");
                // console.log("login Button : ", this.validateLogin.current);
        
                let enteredEmail = this.state.enterEmail;
                let enteredPass =   this.state.enterPass;
        
                if(enteredEmail === this.userData.email && enteredPass === this.userData.pass) {
                    this.setState({navPath: "/participate"});
                    localStorage.setItem("userLoginStatus", true);
                    console.log("Logged In successful")
                } else {
                    alert("Invalid Username or password");
                }

                console.log(localStorage.getItem("userLoginStatus"));
            }
    

        return (
            <div className="row justify-content-center">
                <div className="col-4">
                    <form>
                        <div className="row card my-2 px-2 py-2">
                            <div className="col text-center h4 mt-2 mb-4">
                                Sign In
                            </div>
                            <div className="row my-2">
                                <div className="col-3">
                                    <label className="ml-2 font-weight-bold">Email-Id :</label>
                                </div>
                                <div className="col-9">
                                    <input type="email" className="form-control" value={this.state.enterEmail} onChange={(e) => this.setState({enterEmail: e.target.value})} />
                                </div>
                            </div>
                            <div className="row my-2">
                                <div className="col-3">
                                    <label  className="ml-2 font-weight-bold">Password :</label>
                                </div>
                                <div className="col-9">
                                    <input type="password" className="form-control" value={this.state.enterPass} onChange={(e) => this.setState({enterPass: e.target.value})} />
                                </div>
                            </div>
                            <div className="row my-2 justify-content-center">
                                <div className="col-auto">
                                    <NavLink key="navLink" className="btn btn-primary" type="button" to={this.state.navPath} ref={this.validateLogin} onClick={() => handleLoginClick()} >Login</NavLink>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}
