import React, { Component } from 'react'
import Validation from "./validation"   // import Validation component for display validation status

export default class Range extends Component {

    render() {

        let currentAnswer = this.props.currentAnswer;
        
        // if(this.state.range_value !== this.props.currentAnswer.option_id) {
        //     this.setState({range_value: this.props.currentAnswer.option_id}); 
        // }

        // console.log("current Answer : ", this.props.currentAnswer.option_id);

        let options = this.props.question_object.Options.map((current_option) => {

            let currentOptionId = current_option.option_id;
            let currentOptionTitle = current_option.option_title;

            if(currentAnswer[currentOptionId]) {
                return (
                    <div className="col-12" key={currentOptionId}>
                        <div className="row">
                            <div className="col-2">
                                <label className="font-weight-bold">{currentOptionTitle}</label>
                            </div>
                            <div className="col-9">
                                <input type="range" id={currentOptionId} min={1} max={100} step={1} value={currentAnswer[currentOptionId]} defaultValue={current_option.option} name="kbc_option" onChange={
                                    (e) => {
                                        this.setState({range_value: e.target.value});
                                        this.props.rangeAnswer(e);
                                    }
                                } className="form-control" />
                            </div>
                            <div className="col-1">
                                <label htmlFor={currentOptionId}>{currentAnswer[currentOptionId] + "%"}</label>
                            </div>
                        </div>
                    </div>
                );
            }
            
            return (
                <div className="col-12" key={currentOptionId}>
                    <div className="row">
                        <div className="col-2">
                            <label className="font-weight-bold">{currentOptionTitle}</label>
                        </div>
                        <div className="col-9">
                            <input type="range" id={currentOptionId} min={1} max={100} step={1} defaultValue={current_option.option} name="kbc_option" onChange={
                                (e) => {this.setState({range_value: e.target.value});
                                    this.props.rangeAnswer(e);
                                }
                            } className="form-control" />
                        </div>
                        <div className="col-1">
                            <label htmlFor={currentOptionId}>{current_option.option + "%"}</label>
                        </div>
                    </div>
                </div>
            );
        });

        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                <div className="row my-4">
                    {options}  
                </div>
                <Validation status={this.props.validationWarning} type="range" />
            </React.Fragment>
        )
    }
}
