import React, { Component } from 'react'
import Validation from "./validation"   // import Validation component for display validation status

export default class SelectBox extends Component {
    render() {

        let currentAnswer = this.props.currentAnswer;

        // let selectbox_options = this.props.question_object.Options.map((current_option) => {
        //     let currentOptionId = current_option.option_id;
        //     return(
        //         <option key={currentOptionId} id={currentOptionId} value={current_option.option} 
        //         >{current_option.option}</option>
        //     );
        // });

        let multiple_dropdown = this.props.question_object.Options.map((current_option1) => {

            console.log("current Option 1: ", current_option1);
            let selectbox_options = current_option1.options.map((current_option) => {
                let currentOptionId = current_option.option_id;
                if(currentAnswer[currentOptionId]) {
                    return(
                        <option key={currentOptionId} id={currentOptionId} value={current_option.option} selected
                        >{current_option.option}</option>
                    );
                }
                return(
                    <option key={currentOptionId} id={currentOptionId} value={current_option.option} 
                    >{current_option.option}</option>
                );
            });

            return(
                <div className="col-12 my-2">
                    <div className="row">
                        <div className="col-2">
                            <label className="font-weight-bold">{current_option1.option_title}</label>
                        </div>
                        <div className="col-10">
                            <select className="form-control" onChange={this.props.selectAnswer}>
                                <option>Select</option>
                                {selectbox_options}
                            </select>
                        </div>
                    </div>
                </div>
            );
        });

        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                
                <div className="row my-4">
                    {multiple_dropdown}                
                </div>
                <Validation status={this.props.validationWarning} type="select" />
            </React.Fragment>
        )
    }
}
