import React from 'react'
import { Component } from 'react';

const withLoginCheck = (UnVarifiedComponent) => {

    class VarifiedComponent extends Component {

        state = {
            loginStatus : false
        }

        componentDidMount = () => {
            console.log("withLoginCheck.js login status :",localStorage.getItem("userLoginStatus"));
            console.log("withLoginCheck");
            if(!localStorage.getItem("userLoginStatus")) {
                localStorage.setItem("userLoginStatus", false)
            }
        }

        render() {
            return <UnVarifiedComponent {...this.state} />;
        }
    }
    return VarifiedComponent;
}

export default withLoginCheck;